
import React from 'react'
import { useEffect, useState } from 'react'
import axios from 'axios';

const Api = () => {
       const [repos, setRepos] = useState({data:[]});
       const [search, setSearch] = useState('');
    
       useEffect(() => {
         const fetchData = async () => {
             const response = await axios("https://dummyapi.io/data/api/user",{
                method:"get",
                headers:{
                    // "app-id":"60b6023c98e5768341aefad0"
                    "app-id":"60df1edf9eff0018a8497ae3"
                }
            });
             setRepos(response.data);
        }
         fetchData();
       }, []);
       const reposValue = repos.data;

       const filterValue = reposValue.filter(val=>{
           return val.firstName.toLowerCase().includes(search.toLowerCase()) || val.lastName.toLowerCase().includes(search.toLowerCase()) || val.email.toLowerCase().includes(search.toLowerCase())
       })
    return (
        <>
        <div className="container-fluid mt-5">
        <input onChange={e=>{setSearch(e.target.value)}} className="form-control" type="text" placeholder="Search" aria-label="Search" />
            <div className="row text-center">
            <div className="bg-pattern-top"></div>
            <div className="bg-pattern-bottom"></div>
            {filterValue.map((resp)=>
            <div className="col-10 col-md-4 mt-5" key={resp.id}>
                <div className="card p-2 ">
                <header>
                    <div className="d-flex align-item-center">
                        <div className="image"><img src={resp.picture} alt="images"/></div>
                        <div className="ml-2 w-100">
                            <h3 className="mb-0 mt-4 textLeft bold-text">{resp.title}.{resp.firstName} {resp.lastName}</h3>
                            <span className="textLeft">{resp.email}</span>
                        </div>
                    </div></header>
                </div>
            </div>
            )}
                {/* <div className="background ">
                 <div className="bg-pattern-top"></div>
                 <div className="bg-pattern-bottom"></div>
                    {filterValue.map((resp)=>
                    <div className="card-container ">
                        <header>
                            <img src={resp.picture} />
                        </header>
                        <h1 className="bold-text">{resp.title}.{resp.firstName} {resp.lastName}</h1>
                        <h2 className="normal-text">{resp.email}</h2>
        
                    </div>
                    )}
                </div>  */}
            </div>
        </div>
        </>
    )
}

export default Api
